.login {
  width: 100vw;
  height: 100vh;
  background: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0) 0%,
      rgba(0, 0, 0, 1) 100%
    ),
    url("https://www.logitheque.com/en/wp-content/uploads/sites/6/2019/07/netflix-image.jpg");
  background-size: cover;
  position: relative;
  .top {
    .wrapper {
      padding: 20px 50px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .logo {
        height: 40px;
      }
    }
  }
  .container {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: white;
    align-items: center;
  }

  form {
    width: 300px;
    height: 300px;
    padding: 30px;
    border-radius: 5px;
    background-color: var(--main-color);
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    input {
      height: 40px;
      border-radius: 5px;
      background-color: gray;
      color: white;
      padding-left: 10px;

      &::placeholder {
        color: lightgray;
      }
    }
    button {
      height: 40px;
      border-radius: 5px;
      border: none;
      color: white;
      font-size: 18px;
      background-color: red;
      font-weight: 500;
      cursor: pointer;
    }

    span {
      color: lightgray;
    }
    b {
      color: white;
    }
  }
}
