.listItem {
  width: 225px;
  height: 120px;
  background-color: var(--main-color);
  margin-right: 5px;
  overflow: hidden;
  cursor: pointer;
  color: white;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  iFrame {
    width: 100%;
    height: 140px;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
  }
  .itemInfo {
    margin-top: 140px;
    display: flex;
    flex-direction: column;
    padding: 5px;
    .icons {
      display: flex;
      margin-bottom: 10px;
      .icon {
        border: 2px solid white;
        padding: 5px;
        border-radius: 50%;
        margin-right: 10px;
        font-size: 16px;
      }
    }

    .infoItemTop {
      display: flex;
      align-items: center;
      margin-bottom: 10px;
      font-size: 14px;
      font-weight: bold;
      color: gray;
      .limit {
        border: 1px solid gray;
        padding: 1px 3px;
        margin: 0px 10px;
      }
    }
    .desc {
      font-size: 13px;
      margin-bottom: 10px;
    }
    .genre {
      font-size: 14px;
      color: lightgray;
    }
  }

  &:hover {
    width: 325px;
    height: 300px;
    -webkit-box-shadow: 0px 0px 22px -2px #000000;
    box-shadow: 0px 0px 22px -2px #000000;
    position: absolute;
    top: -150px;
    border-radius: 5px;

    img {
      height: 140px;
    }
  }
}
